@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.contact-confirm__wrap {
  border: 1px solid #000;
  padding: 1.25rem;
}
@media screen and (min-width: 768px) {
  .contact-confirm__wrap {
    padding: 3.75rem;
  }
}

.contact-confirm__title {
  text-align: center;
}

.contact-confirm__text {
  margin-top: 1.25rem;
}

.contact-confirm__form-wrap {
  margin-top: 1.25rem;
}

.contact-confirm__checkbox {
  margin-top: 1.25rem;
  text-align: center;
}

.contact-confirm__box {
  margin-top: 7px;
  padding: 1.875rem;
  height: 12.5rem;
  border: 1px solid #000;
  background-color: #fff;
  overflow-y: scroll;
}
@media screen and (min-width: 768px) {
  .contact-confirm__box {
    height: 31.25rem;
  }
}

.contact-confirm__checkbox-label .wpcf7-list-item-label {
  position: relative;
  display: inline-block;
  padding-left: 1.875rem;
  margin-top: 1.25rem;
}

.wpcf7-list-item-label::before {
  content: "";
  position: absolute;
  display: block;
  border: 1px solid #000;
  width: 1.25rem;
  height: 1.25rem;
  transform: translateY(-50%);
  top: 50%;
  left: -0.125rem;
}

.wpcf7-list-item-label::after {
  content: "";
  position: absolute;
  border-left: 3px solid #000;
  border-bottom: 3px solid #000;
  width: 1rem;
  height: 0.625rem;
  transform: translateY(-50%) rotate(-45deg);
  top: 50%;
  left: 0.125rem;
  opacity: 0;
}

.contact-confirm__checkbox-label input:checked + .wpcf7-list-item-label::after {
  opacity: 1;
}

.contact-confirm__required {
  background-color: #c94800;
  color: #fff;
  display: inline-block;
  font-size: 0.75rem;
  font-weight: normal;
  letter-spacing: 0.4em;
  margin-top: 0.625rem;
  padding: 0.125rem 0.84375rem;
}
@media screen and (min-width: 768px) {
  .contact-confirm__required {
    margin-right: 0.5rem;
    margin-top: 0rem;
  }
}

.contact-confirm__button-wrap {
  margin-top: 1.25rem;
  text-align: center;
}

.contact-confirm__link {
  position: relative;
  padding: 1.25rem 1.875rem;
  display: inline-block;
  max-width: 12.5rem;
  width: 100%;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #E0E0E0;
  cursor: pointer;
  background-image: none;
  border: none;
  text-decoration: none;
  transition: 0.3s;
}

.contact-confirm__link:disabled {
  pointer-events: none;
  cursor: default;
}

.contact-confirm__link::before {
  position: absolute;
  content: "";
  top: 50%;
  right: 1.875rem;
  width: 0.625rem;
  height: 0.625rem;
  border-top: 0.125rem solid #fff;
  border-right: 0.125rem solid #fff;
  transform: translateY(-50%) rotate(45deg);
  transition: 0.3s;
}

.contact-confirm__checkbox-label:has(.privacy-check:disabled) {
  cursor: default;
  pointer-events: none;
}

.contact-form__wrap {
  padding-bottom: 1.5rem;
  border-bottom: 1px dotted #000;
}
@media screen and (min-width: 768px) {
  .contact-form__wrap {
    padding-bottom: 2.5rem;
  }
}

.contact-form__wrap:not(:first-child) {
  margin-top: 1.3125rem;
}
@media screen and (min-width: 768px) {
  .contact-form__wrap:not(:first-child) {
    margin-top: 2.5rem;
  }
}

@media screen and (min-width: 768px) {
  .contact-form__label-wrap {
    display: flex;
  }
}

.contact-form__label {
  font-size: 1rem;
  color: #000;
}
@media screen and (min-width: 768px) {
  .contact-form__label {
    max-width: 13.625rem;
    width: 100%;
  }
}

.contact-form__address-wrap.contact-form__input {
  margin-top: 1.25rem;
}

.contact-form__label-info {
  display: flex;
  justify-content: space-between;
}

.contact-form__label span {
  font-size: 0.75rem;
  font-weight: normal;
  color: #fff;
  background-color: #c94800;
  padding: 0.125rem 0.84375rem;
  display: inline-block;
  margin-left: 0.5rem;
  letter-spacing: 0.4em;
}

.contact-form__input {
  width: 100%;
  margin-top: 1rem;
  flex-grow: 1;
}
@media screen and (min-width: 768px) {
  .contact-form__input {
    margin-top: 0rem;
    margin-left: 2.5rem;
  }
}

.contact-form__input input {
  background-color: #F5F5F5;
  padding: 0.5rem 1rem 0.5rem 1rem;
  width: 100%;
}

.contact-form__input textarea {
  background-color: #F5F5F5;
  padding: 0.5rem 1rem 0.5rem 1rem;
  width: 100%;
  height: 12.5rem;
}

.contact-form__input .contact-form__input-data {
  margin-left: 1.25rem;
}

.contact-form__input .contact-form__input--address2 {
  margin-top: 0.625rem;
}

.contact-form__yubin {
  display: flex;
}

.contact-form__checkbox {
  margin-top: 0.75rem;
}
@media screen and (min-width: 768px) {
  .contact-form__checkbox {
    margin-top: 0rem;
    margin-left: 2.5rem;
  }
}

.contact-form__checkbox label:not(:first-child) {
  margin-top: 0.25rem;
}

.contact-form__checkbox input + span {
  position: relative;
  display: inline-block;
  padding-left: 1.375rem;
  font-size: 1rem;
}

.contact-form__checkbox input + span::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #000;
  display: block;
  width: 0.875rem;
  height: 0.875rem;
}

.contact-form__checkbox input + span::after {
  content: "";
  margin-top: -0.125rem;
  position: absolute;
  top: 55%;
  left: 0.125rem;
  transform: translateY(-50%) rotate(-45deg);
  display: block;
  width: 0.5625rem;
  height: 0.3125rem;
  border-bottom: 2px solid #c94800;
  border-left: 2px solid #c94800;
  transition: 0.3s;
  opacity: 0;
}

.contact-form__checkbox input:checked + span::after {
  opacity: 1;
}

.contact-confirm__button {
  margin-top: 3.75rem;
  text-align: center;
  position: relative;
}

.contact-confirm__button .wpcf7-spinner {
  display: none !important;
}

.contact-confirm__button .button {
  color: #000;
  transition: color 0.3s ease;
  position: relative;
  background-image: none;
  border: none;
  color: #fff;
  background-color: blue;
  cursor: pointer;
  display: inline-block;
  max-width: 12.5rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.3s;
  width: 100%;
}

.contact-confirm__button .button::before {
  border-right: 0.125rem solid #fff;
  border-top: 0.125rem solid #fff;
  content: "";
  height: 0.625rem;
  position: absolute;
  right: 1.875rem;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  transition: 0.3s;
  width: 0.625rem;
}

.contact-confirm__button .button input {
  padding: 1.25rem;
  width: 100%;
  color: #fff;
}

.button:has(.submit-button:disabled) {
  cursor: default;
  pointer-events: none;
  background-color: #E0E0E0;
}

.contact-form__radio {
  margin-left: 0rem;
  margin-top: 1.25rem;
}
@media screen and (min-width: 768px) {
  .contact-form__radio {
    margin-left: 2.5rem;
    margin-top: 0rem;
  }
}

[type=radio] + span {
  cursor: pointer;
  display: inline-block;
  line-height: 1;
  margin: 0 2.1875rem 0 0;
  padding: 0 0 0 1.75rem;
  position: relative;
}

[type=radio] + span::before {
  background: #fff;
  border: 1px solid #707070;
  border-radius: 50%;
  content: "";
  display: block;
  height: 1.125rem;
  left: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 1.125rem;
}

[type=radio]:checked + span::after {
  background: #000;
  border-radius: 50%;
  content: "";
  display: block;
  height: 0.75rem;
  left: 0.1875rem;
  opacity: 1;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 0.75rem;
}

