@use "global" as *;


.contact-form__wrap {
    padding-bottom: rem(24);
    border-bottom: 1px dotted #000;

    @include mq("md") {
        padding-bottom: rem(40);
    }
}

.contact-form__wrap:not(:first-child) {
    margin-top: rem(21);

    @include mq("md") {
        margin-top: rem(40);
    }
}

.contact-form__label-wrap {
    @include mq("md") {
        display: flex;
    }
}

.contact-form__label {
    font-size: rem(16);
    color: #000;

    @include mq("md") {
        max-width: rem(218);
        width: 100%;
    }
}

.contact-form__address-wrap.contact-form__input {
    margin-top: rem(20);
}

.contact-form__label-info {
    display: flex;
    justify-content: space-between;
}

.contact-form__label span {
    font-size: rem(12);
    font-weight: normal;
    color: #fff;
    background-color: #c94800;
    padding: rem(2) rem(13.5);
    display: inline-block;
    margin-left: rem(8);
    letter-spacing: 0.4em;
}

.contact-form__input {
    width: 100%;
    margin-top: rem(16);
    flex-grow: 1;

    @include mq("md") {
        margin-top: rem(0);
        margin-left: rem(40);
    }
}

.contact-form__input input {
    background-color: #F5F5F5;
    padding: rem(8) rem(16) rem(8) rem(16);
    width: 100%;

}

.contact-form__input textarea {
    background-color: #F5F5F5;
    padding: rem(8) rem(16) rem(8) rem(16);
    width: 100%;
    height: rem(200);
}

.contact-form__input .contact-form__input-data {
    margin-left: rem(20);
}

.contact-form__input .contact-form__input--address2 {
    margin-top: rem(10);
}

.contact-form__yubin {
    display: flex;
}

.contact-form__checkbox {
    margin-top: rem(12);

    @include mq("md") {
        margin-top: rem(0);
        margin-left: rem(40);
    }
}

.contact-form__checkbox label:not(:first-child) {
    margin-top: rem(4);
}

.contact-form__checkbox input+span {
    position: relative;
    display: inline-block;
    padding-left: rem(22);
    font-size: rem(16);
}

.contact-form__checkbox input+span::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background: #fff;
    border: 1px solid #000;
    display: block;
    width: rem(14);
    height: rem(14);
}

.contact-form__checkbox input+span::after {
    content: "";
    margin-top: rem(-2);
    position: absolute;
    top: 55%;
    left: rem(2);
    transform: translateY(-50%) rotate(-45deg);
    display: block;
    width: rem(9);
    height: rem(5);
    border-bottom: 2px solid #c94800;
    border-left: 2px solid #c94800;
    transition: 0.3s;
    opacity: 0;
}

.contact-form__checkbox input:checked+span::after {
    opacity: 1;
}

//ボタン
.contact-confirm__button {
    margin-top: rem(60);
    text-align: center;
    position: relative;
}

.contact-confirm__button .wpcf7-spinner {
    display: none !important;
}

.contact-confirm__button .button {
    color: #000;
    transition: color 0.3s ease;
    position: relative;
    background-image: none;
    border: none;
    color: #fff;
    background-color: blue;
    cursor: pointer;
    display: inline-block;
    max-width: 12.5rem;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.3s;
    width: 100%;
}


.contact-confirm__button .button::before {
    border-right: 0.125rem solid #fff;
    border-top: 0.125rem solid #fff;
    content: "";
    height: 0.625rem;
    position: absolute;
    right: rem(30);
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
    transition: 0.3s;
    width: 0.625rem;
}


.contact-confirm__button .button input {
    padding: rem(20);
    width: 100%;
    color: #fff;
}

.button:has(.submit-button:disabled) {
    cursor: default;
    pointer-events: none;
    background-color: #E0E0E0;
}


//ラジオボタン
.contact-form__radio {
    margin-left: rem(0);
    margin-top: rem(20);

    @include mq(md) {
        margin-left: rem(40);
        margin-top: rem(0);
    }
}

[type=radio]+span {
    cursor: pointer;
    display: inline-block;
    line-height: 1;
    margin: 0 2.1875rem 0 0;
    padding: 0 0 0 1.75rem;
    position: relative;
}

[type=radio]+span::before {
    background: #fff;
    border: 1px solid #707070;
    border-radius: 50%;
    content: "";
    display: block;
    height: 1.125rem;
    left: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.125rem;
}

[type=radio]:checked+span::after {
    background: #000;
    border-radius: 50%;
    content: "";
    display: block;
    height: 0.75rem;
    left: 0.1875rem;
    opacity: 1;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 0.75rem;
}

.wpcf7-list-item {
    margin-left: 0;
}