@use "global" as *;

.thanks__content {
    background-color: #F5F5F5;
    padding: rem(40) rem(30);
}

.thanks__button-wrap {
    margin-top: rem(50);
    text-align: center;
}

.thanks__link {
    position: relative;
    padding: rem(20) rem(30);
    display: inline-block;
    max-width: rem(200);
    width: 100%;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    background-color: #E0E0E0;
    cursor: pointer;
    background-image: none;
    border: none;
    text-decoration: none;
    transition: 0.3s;
}