@use "global" as *;

.contact-confirm__wrap {
    border: 1px solid #000;
    padding: rem(20);

    @include mq("md") {
        padding: rem(60);
    }
}

.contact-confirm__title {
    text-align: center;
}

.contact-confirm__text {
    margin-top: rem(20);
}

.contact-confirm__form-wrap {
    margin-top: rem(20);
}

.contact-confirm__checkbox {
    margin-top: rem(20);
    text-align: center;

}

.contact-confirm__box {
    margin-top: 7px;
    padding: rem(30);
    height: rem(200);
    border: 1px solid #000;
    background-color: #fff;
    overflow-y: scroll;

    @include mq("md") {
        height: rem(500);
    }
}


.contact-confirm__checkbox-label .wpcf7-list-item-label {
    position: relative;
    display: inline-block;
    padding-left: rem(30);
    margin-top: rem(20);
}


.wpcf7-list-item-label::before {
    content: "";
    position: absolute;
    display: block;
    border: 1px solid #000;
    width: rem(20);
    height: rem(20);
    transform: translateY(-50%);
    top: 50%;
    left: rem(-2);

}

.wpcf7-list-item-label::after {
    content: "";
    position: absolute;
    border-left: 3px solid #000;
    border-bottom: 3px solid #000;
    width: rem(16);
    height: rem(10);
    transform: translateY(-50%) rotate(-45deg);
    top: 50%;
    left: rem(2);
    opacity: 0;
}


.contact-confirm__checkbox-label input:checked+.wpcf7-list-item-label::after {
    opacity: 1;
}

.contact-confirm__required {
    background-color: #c94800;
    color: #fff;
    display: inline-block;
    font-size: 0.75rem;
    font-weight: normal;
    letter-spacing: 0.4em;
    margin-top: rem(10);
    padding: 0.125rem 0.84375rem;

    @include mq("md") {
        margin-right: rem(8);
        margin-top: rem(0);
    }
}

.contact-confirm__button-wrap {
    margin-top: rem(20);
    text-align: center;
}

.contact-confirm__link {
    position: relative;
    padding: rem(20) rem(30);
    display: inline-block;
    max-width: rem(200);
    width: 100%;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    background-color: #E0E0E0;
    cursor: pointer;
    background-image: none;
    border: none;
    text-decoration: none;
    transition: 0.3s;

}

.contact-confirm__link:disabled {
    pointer-events: none;
    cursor: default;
}

.contact-confirm__link::before {
    position: absolute;
    content: "";
    top: 50%;
    right: rem(30);
    width: rem(10);
    height: rem(10);
    border-top: rem(2) solid #fff;
    border-right: rem(2) solid #fff;
    transform: translateY(-50%) rotate(45deg);
    transition: .3s;
}

.contact-confirm__checkbox-label:has(.privacy-check:disabled) {
    cursor: default;
    pointer-events: none;

}